a {
	cursor: pointer;
	color: $color__link;
	text-decoration: none;
	transition: 0.3s;

	&:focus {
		outline: 5px auto -webkit-focus-ring-color;
		outline-offset: -2px;
	}

	&:hover,
	a:active {
		color: $color__link-hover;
		text-decoration: none;
		outline: 0;
	}
}

