/*----------------
		Font
------------------*/

// Global theme font.
$font__main: 'Open Sans',
sans-serif;

// Global code font
$font__code: 'Open Sans',
sans-serif,
monospace;

// Fallback font when font is not loaded via WebFonts JS.
$font__fallback: sans-serif;

// Base line height.
$font__base-line-height: 30px;

h5 {
	line-height: 1.4;
}